.header {
  width: 100%;
  background-color: $background-black;
  color: $text-white;
  padding: 14px 0;

  &__wrapper {
    @include flex(space-between, center);
  }

  &__nav {
    width: 472px;
    margin: 0 auto 0 60px;
  }

  &__copyright {
    margin: 0 0 0 50px;
    flex: 0 0 auto;

    @include media-tablet {
      display: none;
    }
  }
}
